body {
  margin: 0;
  padding: 0;
  font-family: "Allerta+Stencil",Roboto, -apple-system, BlinkMacSystemFont, "Segoe UI", "Allerta+Stencil", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.nav-wrapper{
  text: black;
}

.pic-container{
  height: 300px;
  width: 300px;
  border-radius: 50%;
  box-shadow: 2px 5px 3px 3px black;
}


.img-fluid {
    height:auto;
    width:100%;
}

@media(min-width:780px) {
  .img-fluid{
    height:85vh
  }
}

.footer-wrapper{
    padding-top: 1.0rem;
}

@media only screen and (max-width:425px){
  .footer-wrapper{
    padding-top: 0px;
  }
}

.image-wrapper{
  width:180px;
}

.skills-container{
  width:240px;
  height:225px;
  -webkit-align-items: center;
          align-items: center;
}

.skill-name-wrapper{
  color:#FF9933;
  margin-right: auto;
  margin-left: auto;
  margin-top: -23px;
  height:50px;
  width:150px;
  background-color: #212121;
}

/* .project-card-wrapper{
  width:40%;
} */


@media only screen and (min-width:770px){
  .project-card-wrapper{
    width:40%;
  }
}

@media only screen and (min-width:426px) and (max-width:768px){
  .project-card-wrapper{
    width:75%;
  }
}

.App {
  text-align: center;
  background-color: #ddd;
  font-family: "Poppins";
}

